.NewsBox{
    display: flex;
    flex-direction: column;
    padding:5px;
    background-color: rgb(255, 255, 255);
    height:45vh
    
}
.blogTitle{
    display: block;
    margin:0px;
    width:fit-content;
    height:30%;
    font-weight: bold;
    font-size: medium;
    text-align: justify;
    font-size: 100%;
    align-content: flex-start;
    overflow: hidden;
    cursor: pointer;
}
.blogTitle p{
    cursor: pointer;
}
.img{
    margin:0px;
    height:70%;
    cursor: pointer;
}
